const getTagsStorage = (key: string) => {
  if (key === 'inTags_react') {
    if (window.localStorage.getItem(key) === null) {
      return [
        {className: 'parttime', remake: '', id: 1},
        {className: 'wage', remake: '', id: 2},
        {className: 'licai', remake: '', id: 3},
        {className: 'otherrevenue', remake: '', id: 4},
      ];
    } else {
      return JSON.parse(window.localStorage.getItem(key) as string);
    }
  } else if (key === 'outTags_react') {
    if (window.localStorage.getItem(key) === null) {
      return [
        {className: 'catering', remake: '', id: 1},
        {className: 'shopping', remake: '', id: 2},
        {className: 'dayuse', remake: '', id: 3},
        {className: 'traffic', remake: '', id: 4},
        {className: 'sport', remake: '', id: 5},
        {className: 'pet', remake: '', id: 6},
        {className: 'recreation', remake: '', id: 7},
      ];
    } else {
      return JSON.parse(window.localStorage.getItem(key) as string);
    }
  }

};

export default getTagsStorage;