import dayjs from "dayjs";

function getDateAndWeek(dateString: string) {
  const dayApi = dayjs(dateString);
  const string = dayApi.format("MMDD");
  const weekList = [
    "",
    "",
    "",
    "",
    "",
    "",
    "",
  ];
  const week = weekList[dayApi.day()];
  return string + " " + week;
}

export default getDateAndWeek;
